___CSS_LOADER_EXPORT___.push([module.id, "\n.selected-type {\n        background-color: transparent;\n        color: #212121;\n}\n.custom-select .items {\n        color: #212121;\n        border-radius: 8px;\n        border: 1px solid #E3E3E3;\n        overflow: hidden;\n        background-color: #FFFFFF;\n        box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.2);\n}\n.custom-select .items .option {\n        font-size: 14px;\n        padding-left: 12px;\n}\n.custom-select .items .option:hover {\n        background-color: #EEEEEE;\n        color: #212121;\n}\n.select-account-type i {\n        font-size: 16px;\n        color: #BCBCBC;\n}\n.org-tag-name {\n        align-items: center;\n        padding: 5px 8px;\n        background: #D0D1E9;\n        display: flex;\n        justify-content: center;\n        border-radius: 20px;\n        margin-right: 4px;\n        font-size: 14px;\n        color: #2F3160;\n        white-space: nowrap;\n        font-weight: 500;\n}\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/components/dialogs/backoffice/manageOrganization.vue"],"names":[],"mappings":";AAiTI;QACI,6BAA6B;QAC7B,cAAc;AAClB;AACA;QACI,cAAc;QACd,kBAAkB;QAClB,yBAAyB;QACzB,gBAAgB;QAChB,yBAAyB;QACzB,0CAA0C;AAC9C;AACA;QACI,eAAe;QACf,kBAAkB;AACtB;AACA;QACI,yBAAyB;QACzB,cAAc;AAClB;AACA;QACI,eAAe;QACf,cAAc;AAClB;AACA;QACI,mBAAmB;QACnB,gBAAgB;QAChB,mBAAmB;QACnB,aAAa;QACb,uBAAuB;QACvB,mBAAmB;QACnB,iBAAiB;QACjB,eAAe;QACf,cAAc;QACd,mBAAmB;QACnB,gBAAgB;AACpB","sourcesContent":["<script setup lang=\"ts\">\n    import { IEditorTypes, IOrganization, IOrganizationFeatures } from \"../../../../../../../common/qCloudTemp/membership\";\n    import { IOrganizationActions } from \"../../../../../../../common/qCloudTemp/quickCloud\";\n    import { ref, computed, onBeforeMount } from \"vue\";\n    import { ITags } from \"../../../../../../../common/qCloudTemp/quickCloud\";\n    import { container } from \"../../../../../domain/core/diContainer\";\n    import { IBackofficeUseCase } from \"../../../../../domain/useCase/IBackofficeUseCase\";\n    import { IUseCaseExecutor } from \"../../../../../domain/useCase/IUseCaseExecutor\";\n    import { IViewModel } from \"../../../../../domain/viewModel/IViewModel\";\n    import CustomSelect from \"../../customSelect.vue\"\n\n    interface IManageOrgData {\n        organization: IOrganization,\n    }\n    const executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\n    const backofficeUseCase = container.resolve<IBackofficeUseCase>(IBackofficeUseCase);\n    const viewModel = container.resolve<IViewModel>(IViewModel);\n\n    const props = defineProps<IManageOrgData>();\n    const organization = ref(props.organization)\n    const selectableTags = ref<Array<ITags>>();\n    const organizationTags = ref<Array<ITags>>();\n    const selectedTags = ref<Array<ITags>>([]);\n\n\n    const createAppLimit = ref(organization.value.limits?.createApp);\n    const accountType = ref(organization.value.accountType);\n    const appQuick = ref(organization.value.applications.quick);\n    const appFlow = ref(organization.value.applications.flow);\n    const appEntity = ref(organization.value.applications.entity);\n    const appProcess = ref(organization.value.applications.process);\n    const actionPublicCloud = ref(organization.value.actions?.publish?.targets?.azure);\n    const actionPlateauGCP = ref(organization.value.actions?.publish?.targets?.plateauGCP);\n    const qjsonLoading = ref(organization.value.features?.qjsonLoading);\n    const multipleCss = ref(organization.value.features?.multipleCss);\n\n    onBeforeMount(() => {\n        getOrganizationTags();\n        arrangeTags();\n    })\n    const emit = defineEmits([\"close\"]);\n    function close() {\n        emit(\"close\");\n    }\n    function setAccountType(value: 'premium' | 'beta' | undefined) {\n        accountType.value = value;\n    }\n    function getOrganizationTags() {\n        const tagsDict: Record<string, ITags> = {};\n        viewModel.backoffice?.tags?.forEach(tag => tagsDict[tag.ID] = tag);\n        organizationTags.value = organization.value?.tags?.map(tag => tagsDict[tag]);\n    }\n    function arrangeTags(remove?: boolean, toRemove?: ITags) {\n        if(remove) {organizationTags.value = organizationTags.value?.filter(tag => tag.ID != toRemove?.ID)}\n        const tagsInTheBox = organizationTags.value?.concat(selectedTags.value);\n        selectableTags.value = viewModel.backoffice?.tags?.filter(tag => !tagsInTheBox?.includes(tag));\n\n    }\n    function setTags(tags: Array<ITags>) {\n        selectedTags.value = tags;\n    }\n    function save() {\n        executor.execute( async () => {\n            const applications: IEditorTypes = { quick: appQuick.value, flow: appFlow.value, entity: appEntity.value,  process: appProcess.value };\n            const actions: IOrganizationActions = {publish: {targets:{azure: actionPublicCloud.value!, plateauGCP: actionPlateauGCP.value!}}};\n            const environmentPrefix = actionPlateauGCP.value ? 'studio-' + organization.value.name.toLowerCase() : '';\n            const features: IOrganizationFeatures = {qjsonLoading: qjsonLoading.value, multipleCss: multipleCss.value};\n            const selectedTagIDs = selectedTags.value.map(tag => tag.ID);\n            await backofficeUseCase.updateOrganization(organization.value.id, {\n                accountType: accountType.value!, createAppLimit: createAppLimit.value!, actions: actions,\n                applications: applications, environmentPrefix: environmentPrefix, features: features, tags: selectedTagIDs\n            });\n            organization.value.limits!.createApp = createAppLimit.value;\n            organization.value.accountType = accountType.value;\n            organization.value.applications = applications;\n            organization.value.actions = actions;\n            organization.value.features = features;\n            organization.value.tags = selectedTagIDs;\n            close();\n        }, {loading: true})\n    }\n</script>\n\n<template>\n    <div class=\"container\">\n        <div class=\"app-limit\">\n            <span>Number of applications that can be created</span>\n            <input type=\"number\" v-model=\"createAppLimit\"/>\n        </div>\n        <div class=\"account-type\">\n            <label>Account Type</label>\n            <CustomSelect :options=\"['premium', 'beta']\" class=\"select-account-type\" :default=\"organization.accountType\" @input=\"setAccountType\"></CustomSelect>\n        </div>\n        <div class=\"editors\">\n            <label>Editors</label>\n           <div class=\"editor-type\">\n                <span class=\"editor-type-name\">UI</span>\n                <input class=\"editor-type-switch round\" type=\"checkbox\" v-model=\"appQuick\"/>\n           </div>\n           <div class=\"editor-type\">\n                <span class=\"editor-type-name\">Flow</span>\n                <input class=\"editor-type-switch round\" type=\"checkbox\" v-model=\"appFlow\"/>\n           </div>\n           <div class=\"editor-type\">\n                <span class=\"editor-type-name\">Data Model</span>\n                <input class=\"editor-type-switch round\" type=\"checkbox\" v-model=\"appEntity\"/>\n           </div>\n           <div class=\"editor-type\">\n                <span class=\"editor-type-name\">Worklflow</span>\n                <input class=\"editor-type-switch round\" type=\"checkbox\" v-model=\"appProcess\"/>\n           </div>\n           \n        </div>\n        <div class=\"publish-to\">\n            <label>Publish To</label>\n            <div class=\"publish-type\">\n                <span class=\"publish-type-name\">Public  Cloud</span>\n                <input class=\"publish-type-switch round\" type=\"checkbox\" v-model=\"actionPublicCloud\"/>\n           </div>\n            <div class=\"publish-type\">\n                <span class=\"publish-type-name\">Plateau Platform Playground</span>\n                <input class=\"publish-type-switch round\" type=\"checkbox\" v-model=\"actionPlateauGCP\"/>\n           </div>\n        </div>\n        <div class=\"features\">\n            <label>Features</label>\n            <div class=\"publish-type\">\n                <span class=\"publish-type-name\">Qjson Loading</span>\n                <input class=\"publish-type-switch round\" type=\"checkbox\" v-model=\"qjsonLoading\"/>\n           </div>\n            <div class=\"publish-type\">\n                <span class=\"publish-type-name\">Multiple CSS</span>\n                <input class=\"publish-type-switch round\" type=\"checkbox\" v-model=\"multipleCss\"/>\n           </div>\n        </div>\n        <div class=\"tags\">\n            <label>Tags</label>\n            <div>\n                <CustomSelect :options=\"selectableTags\" class=\"select-account-type\" :placeholder=\"'Insert Tags'\" :defaultMultiple=\"organizationTags\"  :multiple=\"true\"\n                              :multipleSelectedClass=\"'org-tag-name'\" @afterClick=\"arrangeTags\" @input=\"setTags\"></CustomSelect>\n            </div>\n        </div>\n        <div class=\"buttons-div\">\n            <button class=\"cancel-button\" @click=\"close()\">Cancel</button>\n            <button class=\"save-button\" @click=\"save()\">SAVE</button>\n        </div>\n    </div>\n</template>\n\n\n<style scoped>\n    * {\n        font-family: \"Roboto\";\n    }\n    .container {\n            padding: 32px 20px 20px;\n    }\n    .app-limit {\n        display: flex;\n        align-items: center;\n    }\n    .app-limit span {\n        font-size: 14px;\n        line-height: 16px;\n        color: #212121;\n    }\n    .app-limit input {\n        border: 1px solid #BCBCBC;\n        border-radius: 4px;\n        padding: 8px 8px 8px 12px;\n        width: 70px;\n        height: 40px;\n        margin-left: 111px;\n        outline: none;\n        margin-left: auto;\n    }\n    .account-type {\n        margin: 20px 0;\n    }\n    .editors,\n    .publish-to {\n        margin-bottom: 30px;\n    }\n    .account-type label,\n    .editors label,\n    .publish-to label,\n    .features label\n    .tags label {\n        font-weight: 500;\n        font-size: 16px;\n        line-height: 24px;\n        color: #151233;\n        margin-bottom: 4px;\n    }\n    .buttons-div {\n        display: flex;\n        justify-content: end;\n        margin-top: 52px;\n    }\n    .cancel-button {\n        font-weight: 500;\n        font-size: 16px;\n        text-align: center;\n        color: #757575;\n        display: flex;\n        align-items: center;\n        padding: 8px 16px;\n        border-radius: 4px;\n        margin-right: 4px;\n        border: none;\n        background: transparent;\n    }\n    .save-button {\n        font-weight: 500;\n        font-size: 16px;\n        text-align: center;\n        color: #FFFFFF;\n        background: #151233;\n        display: flex;\n        align-items: center;\n        padding: 8px 16px;\n        border-radius: 4px;\n        border: none;\n    }\n    .editor-type-name,\n    .publish-type-name {\n        color: #212121;\n        font-size: 14px;\n        line-height: 16px;\n    }\n    .editor-type,\n    .publish-type {\n        display: flex;\n        align-items: center;\n        margin-bottom: 12px;\n    }\n    .select-account-type {\n        width: 100%;\n        font-size: 15px;\n        height: 40px;\n        line-height: 40px;\n        border: 1px solid #BCBCBC;\n        border-radius: 4px;\n        padding: 0px 12px;\n    }\n    /*Switch Toggle CSS */\n    .editor-type-switch,\n    .publish-type-switch {\n        position: relative;\n        cursor: pointer;\n        top: 0;\n        bottom: 0;\n        background-color: #ccc;\n        -webkit-transition: 0.4s;\n        transition: 0.4s;\n        margin-right: 13px;\n        margin-left: auto;\n    }\n    .editor-type-switch:after,\n    .publish-type-switch:after {\n        position: absolute;\n        content: \"\";\n        height: 14px;\n        width: 14px;\n        background-color: white;\n        -webkit-transition: 0.4s;\n        transition: 0.4s;\n        border-radius: 50%;\n    }\n    .editor-type-switch:disabled:after,\n    .publish-type-switch:disabled:after {\n        background-color: #757575;\n    } \n    .editor-type-switch:checked,\n    .publish-type-switch:checked {\n        background-color: #151233;\n    }\n    input[type=checkbox] {\n        -webkit-appearance: none;\n        -moz-appearance: none;\n        appearance: none;\n        -webkit-tap-highlight-color: transparent;\n        cursor: pointer;\n    }\n    .editor-type-switch:checked:after,\n    .publish-type-switch:checked:after {\n        transform: translatex(16px);\n    }\n    .editor-type-switch.round::after,\n    .publish-type-switch.round::after {\n        left: 3px;\n        border-radius: 50%;\n    }\n    .editor-type-switch.round,\n    .publish-type-switch.round {\n        width: 36px;\n        height: 20px;\n        border-radius: 34px;\n        padding: 3px;\n    }\n    .custom-select .selected {\n        height: 40px\n    }\n</style>\n<style>\n    .selected-type {\n        background-color: transparent;\n        color: #212121;\n    }\n    .custom-select .items {\n        color: #212121;\n        border-radius: 8px;\n        border: 1px solid #E3E3E3;\n        overflow: hidden;\n        background-color: #FFFFFF;\n        box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.2);\n    }\n    .custom-select .items .option {\n        font-size: 14px;\n        padding-left: 12px;\n    }\n    .custom-select .items .option:hover {\n        background-color: #EEEEEE;\n        color: #212121;\n    }\n    .select-account-type i {\n        font-size: 16px;\n        color: #BCBCBC;\n    }\n    .org-tag-name {\n        align-items: center;\n        padding: 5px 8px;\n        background: #D0D1E9;\n        display: flex;\n        justify-content: center;\n        border-radius: 20px;\n        margin-right: 4px;\n        font-size: 14px;\n        color: #2F3160;\n        white-space: nowrap;\n        font-weight: 500;\n    }\n</style>"],"sourceRoot":""}]);
